.__SignIn {
  display: flex;
  justify-content: center;
}
.__SignIn .__container {
  width: 75%;
  height: 75vh;
  display: flex;
  justify-content: center;
  margin-top: 6em;
  border-radius: 1rem;
  box-shadow: 0 4px 20px rgba(149, 185, 193, 0.2);
  border-radius: 1rem;
}
.__SignIn .__container .__left_side {
  float: left;
  width: 80%;
  text-align: center;
  background-color: #fff;
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
}
.__SignIn .__container .__left_side .__user_image {
  margin-top: 2em;
}
.__SignIn .__container .__left_side .__user_image img {
  width: 85px;
  border-radius: 50%;
}
.__SignIn .__container .__left_side .__form {
  display: flex;
  flex-direction: column;
  margin: 1em 8em;
}
.__SignIn .__container .__left_side .__form input {
  margin-bottom: 0.3em;
  border-radius: 15px;
}
.__SignIn .__container .__left_side .__form input:last-child {
  margin-bottom: 0em;
}
.__SignIn .__container .__left_side .__form p {
  text-align: right;
}
.__SignIn .__container .__left_side .__form button {
  text-transform: none;
  border: none;
  background: #25d366;
  color: #fff;
  border-radius: 15px;
  font-size: 1rem;
  padding: 4px 35px;
  font-weight: 600;
}
.__SignIn .__container .__left_side .__form button:focus {
  outline: none;
}
.__SignIn .__container .__left_side .__form button:hover {
  background-color: #1a9d4b;
}
.__SignIn .__container .__right_side {
  float: right;
  background: #25d366;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
}
.__SignIn .__container .__right_side .__right_content {
  text-align: center;
  padding: 0 5rem;
}
.__SignIn .__container .__right_side .__right_content h2 {
  color: #fff;
  letter-spacing: 1px;
}
.__SignIn .__container .__right_side .__right_content p {
  color: #fff;
  letter-spacing: 1px;
}
.__SignIn .__container .__right_side .__right_content button {
  text-transform: none;
  border: none;
  background-color: #1a9d4b;
  color: #fff;
  border-radius: 4px;
  font-size: 1rem;
  padding: 4px 22px;
  font-weight: 600;
}
.__SignIn .__container .__right_side .__right_content button:focus {
  outline: none;
}
.__SignIn .__container .__right_side .__right_content button:hover {
  background: #169e48;
}

input[type=text],
input[type=password],
input[type=email],
input[type=file] {
  width: 100%;
  padding: 6px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  background: #f1f1f1;
  border: none;
}
input[type=text]:focus,
input[type=password]:focus,
input[type=email]:focus,
input[type=file]:focus {
  outline: none;
  background-color: rgba(91, 255, 152, 0.1803921569);
}
input[type=text]::placeholder,
input[type=password]::placeholder,
input[type=email]::placeholder,
input[type=file]::placeholder {
  color: gray;
  opacity: 1;
  letter-spacing: 1px;
}

.__user__image__profile-icon-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}

.__user__image__profile-icon-container__camera-icon {
  position: relative;
  bottom: 20px;
}
